export default {
	Img_upload(sourceType) {
		return new Promise((resolve, reject) => {
			uni.chooseImage({
				count: 1,
				sizeType: ['original'],
				sourceType: sourceType,
				success: function(res) {
					console.log("");
					resolve(res.tempFilePaths[0]);
				},
				fail: function() {
					reject('');
				}
			})
		})
	},
	/**data 
	 * @param {Object} getDetail   
	 * @param {Object} which_page  
	 * @param {Object} pageSize\   
	 * @param {Object} subType\    1. 2.
	 * * @param {Object} seo\   
	 */
	activity_data(getDetail, which_page, pageSize, status, subType, seo) {
		let par = {};
		par.type = 2; //
		par.subType = subType ? subType : 1;
		par.seo = seo ? seo : ''
		getDetail == true ? par.getDetail = getDetail : '';
		par.which_page = which_page; //
		par.pageSize = pageSize; //1 
		status > 0 ? par.status = status : "";
		return par;
	},
	activity_preview(id,type,subType,getDetail){
		let par={};
		par.id=id;
		// par.type=type;
		// par.subType=subType;
		if(getDetail){
			par.getDetail=getDetail;
		}
		return par;
	},
	/**
	 * @param {Object} type  
	 * @param {Object} seqno 
	 * @param {Object} param =5type=4id
	 */
	form_data(type, seqno, param, param1, rid) {
		let par = {};
		par.token = uni.getStorageSync('token');
		par.goodClassId = uni.getStorageSync('goodClassId'); //id
		par.type = type;
		par.seqno = seqno;
		if (param) {
			par.param = param;
		}
		if (param1) {
			par.param1 = param1;
		}
		if (rid) {
			par.rid = rid;
		}
		return par;
	},
	//
	show_modal(warn) {
		return new Promise((reslove, reject) => {
			uni.showModal({
				title: '',
				content: warn,
				success: function(res) {
					if (res.confirm) {
						reslove(true)
					} else if (res.cancel) {
						reslove(false);
					}
				}
			})
		})
	},
	compare(pro) {
		return function(obj1, obj2) {
			var val1 = obj1[pro];
			var val2 = obj2[pro];
			if (val1 > val2) { //
				return 1;
			} else if (val1 < val2) {
				return -1;
			} else {
				return 0;
			}
		}
	},
	//compare
	list_sort(gcImgList) {
		var b = Array.from(new Set(gcImgList));
		var c = [],
			d = [];
		var count = 0;
		b.map((item) => {
			gcImgList.map(itemC => {
				if (itemC.seqno == item.seqno) {
					d.push(itemC)
				}
			});
			c.push(d);
			d = [];
		});
		var list = [];
		for (var i = 0; i < c.length; i++) {
			if (i == c.length - 1) {
				list.push(c[i]);
			} else if (c[i][0].id == c[i + 1][0].id) {
				continue;
			} else {
				list.push(c[i]);
			}
		}
		return list;
	},
	compareUp(data, propertyName) { // 
		if ((typeof data[0][propertyName]) != "number") { // 
			return function(object1, object2) {
				var value1 = object1[propertyName];
				var value2 = object2[propertyName];
				return value1.localeCompare(value2);
			}
		} else {
			return function(object1, object2) { // 
				var value1 = object1[propertyName];
				var value2 = object2[propertyName];
				return value1 - value2;
			}
		}
	},
	compareDown(data, propertyName) { // 
		if ((typeof data[0][propertyName]) != "number") { // 
			return function(object1, object2) {
				var value1 = object1[propertyName];
				var value2 = object2[propertyName];
				return value2.localeCompare(value1);
			}
		} else {
			return function(object1, object2) { // 
				var value1 = object1[propertyName];
				var value2 = object2[propertyName];
				return value2 - value1;
			}
		}
	}
}
